import React, { useState, useEffect } from "react"

async function getTags(url) {
  let token = process.env.GATSBY_GITHUB_TOKEN
  const response = await fetch(url, {
    headers: { Authorization: "token " + token },
  })
  const json = await response.json()
  return json
}

const top = function(data) {
  let tags = []
  data.forEach((tag, value) => {
    tags.push(tag.tags_url)
  })

  let the_tags = []

  tags.forEach(all_tags => {
    let tg = getTags(all_tags)
    the_tags.push(tg)
  })

  let values = []
  the_tags.forEach((d, i) => {
    d.then(all => {
      if (all.length > 0) {
        values.push({ id: i, name: all[0].name })
      }
    })
  })
  return values
}

const getData = () => {
  const [repos, setRecentlyUpdatedRepos] = useState("no data")
  const [results, setResults] = useState(false)

  async function getRepoList() {
    let token = process.env.GATSBY_GITHUB_TOKEN
    const response = await fetch(
      "https://api.github.com/users/joshayoung/repos",
      { headers: { Authorization: "token " + token } }
    )
    const json = await response.json()
    let t = top(json)
    setRecentlyUpdatedRepos(t)
    setResults(true)
  }

  useEffect(() => {
    getRepoList()
  }, [])

  return { repos, results }
}

export default () => {
  const { repos, results } = getData()
  return (
    <>
      {/* <pre>{JSON.stringify(repos)}</pre> */}
      {results ? (
        <ul>
          {repos.map(val => (
            <li key={val.i}>
              <div dangerouslySetInnerHTML={{ __html: val.name }} />
            </li>
          ))}
        </ul>
      ) : (
        <ul>
          <li>nothing</li>
        </ul>
      )}
    </>
  )
}
